	module.exports = {"data":{"markdownRemark":{"html":"<p>今天国庆，北京举行首都国庆晚会，十分热烈，隆重，似乎比以往都要热烈。广场上灯光通明，五彩缤纷鲜花、人群、欢语、笑声，一片欢腾礼炮、焰火、舞姿、歌声，十面来风。今年的焰火放得特别多，特别好，有些品种过去没有见过，量也大，在天空中布下一大片，绚丽无比，映照着我们，映照着大地，映照着天空。</p>\n<p>一个民族必须有自己隆重的仪式，这是一个民族感觉到自己民族整体性和民族伟大性的最好途径。民族中的各个分子，在日常生活中往往是分散的，或者是在小的群体中，对整体的感觉并不强烈。</p>\n<p>民族的每个群体，在日常岁月中，往往只有文化的、经济的、艺术的联系，而全民族的隆重仪式能够告诉人们更重要的东西，这就是精神的实体和政治的联系。所以世界上许多国家都十分注意自己民族的仪式，而国庆节是最好的契机。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0152.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0152.md","frontmatter":{"title":"10月1日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0152.md absPath of file >>> MarkdownRemark"}}

